.container{
    display: flex;
    align-items: center;
    margin-right:0.3vw;
}

.containerMain{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 0.5vw;
}


.containerDetailPage{
    display: flex;
    align-items: center;
    margin-right:3vw;
}

@media screen and (max-width: 1200px) {
    .container{
        font-size: 16px;
    }
    .containerMain{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-left: 0.5vw;
        margin-right: 0.2vw;
    }
}

@media screen and (max-width: 800px) {  
    .container{
        font-size: 14px;
    }
}

@media screen and (max-width: 400px) {
    .container{
        font-size: 12px;
    }
}